// 공통
// + 더보기
.btn-view-more {
  margin-top: 3.75rem;
}
// 영상, 이미지 리스트
.media-list {
  &.end-state {
    a {
      opacity: .3;
      pointer-events: none;
    }
  }
}
// field list
.list-tit {
  @include coTxtLabelLarge;
  color: $color-sub-02;
  margin-top: 3.75rem;
  & + .c-input-list {
    margin-top: 0.75rem;
  }
}
// tag list
.tag-list {
  display: flex;
  flex-wrap: wrap;
  margin-left: -0.25rem;
  width: calc(100% + 0.5rem);
  li {
    @include coTxtLabelMedium;
    color: $color-main;
    background-color: $color-bg-03;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0.125rem 0.625rem;
    height: 1.5rem;
    text-align: center;
    border-radius: 1.25rem;
    margin: {
      top: 0;
      right: 0.25rem;
      bottom: 0.5rem;
      left: 0.25rem;
    }
    &:last-child {
      margin-right: 0;
    }
  }
  &.type-main-card {
    position: relative;
    width: 100%;
    height: 1.25rem;
    margin: 0;
    display: block;
    overflow-x: auto;
    overflow-y: hidden;
    &::-webkit-scrollbar,
    &::-webkit-scrollbar-thumb {
      display: none;
    }
    ul {
      display: block;
      width: max-content;
      li {
        position: relative;
        padding: {
          top: 0;
          right: 0;
          bottom: 0;
          left: 0.8125rem;
        }
        margin: 0;
        width: auto;
        height: 1.25rem;
        color: #FFF;
        background-color: transparent;
        border-radius: 0;
        float: left;
        &::after {
          content: '/';
          display: block;
          position: absolute;
          left: 0.25rem;
          top: 0;
          width: 0.3125rem;
          height: 1.25rem;
          color: $color-white-40;
        }
        &:first-child {
          padding-left: 0;
          &::after {
            display: none;
            width: 0;
            height: 0;
          }
        }
      }
      &::after {
        content: '';
        display: block;
        clear: both;
        width: 0;
        height: 0;
      }
    }
  }
}
// floating title, tab
.header {
  &.co-header {
    & ~ .flex-container {
      .tit {
        &.type-floating {
          padding-bottom: 0.5rem;
          & + .c-tab {
            &.type-floating {
              position: sticky;
              left: 0;
              top: 84px;
              background-color: #FFF;
              z-index: 9000;
              transition: 
                top $transition-3 ease-in-out,
                padding $transition-3 ease-in-out;
              &.isScroll {
                padding: 0.75rem;
                &::after {
                  content: '';
                  display: block;
                  position: absolute;
                  left: 0;
                  bottom: -2.25rem;
                  width: 100%;
                  height: 2.25rem;
                  background-image: linear-gradient(to bottom, rgba(25, 25, 25, 0.05) 0%, rgba(217, 217, 217, 0) 38.05%);
                  transition: 
                    height $transition-3 ease-in-out,
                    background-image $transition-3 ease-in-out;
                }
              }
              & + .tab-panel {
                &.show {
                  padding-top: 3rem;
                }
              }
            }
          }
        }
      }
    }
    &.scroll-hide {
      & ~ .flex-container {
        .tit {
          &.type-floating {
            & + .c-tab {
              &.type-floating {
                top: -4rem;
                &.isScroll {
                  &::after {
                    bottom: 0;
                    height: 0;
                    background-image: linear-gradient(to bottom, rgba(25, 25, 25, 0) 0%, rgba(217, 217, 217, 0) 38.05%);
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}
// table
.table-wrap {
  position: relative;
  width: 100%;
  overflow-x: auto;
  overflow-y: hidden;
  &::-webkit-scrollbar,
  &::-webkit-scrollbar-thumb {
    display: none;
  }
  table {
    position: relative;
    thead th,
    tbody td {
      color: $color-black-80;
      vertical-align: middle;
    }
    thead {
      border-bottom: 2px solid $color-black-10;
      th {
        @include coTxtLabelLarge;
        text-align: center;
        border-top: 1px solid $color-black-10;
        padding: 0.5rem;
      }
    }
    tbody {
      td {
        @include coTxtBody;
        border-bottom: 1px solid $color-black-10;
        text-align: left;
        padding: 0.75rem;
      }
    }
  }
}

// 전체틀, 타이틀, 탭
.login-block,
.hi-block,
.ni-block,
.mi-block,
.em-block,
.terms-block,
.guide-block {
  position: relative;
  .tit {
    @include coTxtTitleHeadingLarge;
    color: $color-black-80;
    &.type-step {
      display: flex;
      justify-content: space-between;
      align-items: center;
      .step, .step span {
        @include coTxtTitleLarge;
        color: $color-black-20;
      }
      .step {
        span {
          &:first-child {
            color: $color-black-40;
          }
        }
      }
      button {
        &.ico-plus {
          position: relative;
          width: 2.25rem;
          height: 2.25rem;
          background: #FFF;
          box-shadow: 0.25rem 0.25rem 0.5rem rgba(0, 0, 0, 0.1);
          border-radius: 1.5rem;
          &::before,
          &::after {
            content: '';
            display: block;
            position: absolute;
            background-color: $color-sub-01;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
          }
          &::before {
            width: 0.125rem;
            height: 0.75rem;
          }
          &::after {
            width: 0.75rem;
            height: 0.125rem;
          }
        }
      }
    }
  }
  .c-tab {
    &.type-slide {
      margin-left: -1.75rem;
      width: 100vw;
      padding: {
        top: 1.25rem;
        right: 1.75rem;
        bottom: 3.75rem;
        left: 1.75rem;
      }
      &.type-floating {
        padding: 0.75rem 1.75rem;
      }
    }
  }
  .detail-tit {
    @include coTxtTitleHeadingSmall;
    color: $color-black-80;
    margin-bottom: 1.25rem;
  }
  .detail-info {
    position: relative;
    padding: 1.25rem 0;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    span {
      @include coTxtLabelMedium;
      color: $color-sub-01;
      display: flex;
      align-items: center;
      justify-content: flex-start;
      width: auto;
      height: 2rem;
      margin-left: 0.75rem;
      padding-left: 2rem;
      background-image: url('../../images/ico/ico_calendar_01.svg');
      background-size: 2rem 2rem;
      background-position: left center;
      background-repeat: no-repeat;
      &:first-child {
        margin-left: 0;
        background-image: url('../../images/ico/ico_eye_01.svg');
      }
    }
    &::after {
      content: '';
      display: block;
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 1px;
      background-color: $color-black-05;
    }
  }
  .detail-editor {
    position: relative;
    background-color: $color-black-05;
    &.type-vdo {
      padding-bottom: 56.25%; /* 16:9 */
      height: 0;
      iframe {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
      }
    }
  }
  .detail-btn {
    margin-top: 2.5rem;
  }
  .btn-move-list {
    margin-top: 1.25rem;
  }

  // + 등록된 컨텐츠 없음
  .null-contents {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 2.5rem 0;
    img {
      width: 7rem;
      height: auto;
      opacity: .4;
    }
    p {
      @include coTxtBody;
      color: $color-black-40;
      text-align: center;
      margin-top: 0.9375rem;
    }
    &.type-round {
      padding: 1rem 0;
      border: 1px solid $color-black-20;
      border-radius: 0.75rem;
      img {
        width: 5.0625rem;
      }
      p {
        margin-top: 0.75rem;
      }
    }
  }
}
.login-block {
  padding: 3.75rem 1.75rem;
}
.hi-block,
.ni-block,
