// 이용약관
.terms-block {
  .tab-panel {
    &.show {
      .terms-service {
        h2 {
          @include coTxtTitleLarge;
          color: $color-black-80;
          margin-top: 3.75rem;
          &:first-child {
            margin-top: 0;
          }
        }
        h3, h4 {
          @include coTxtLabelLarge;
          color: $color-black-80;
          margin-top: 1.25rem;
          & + p,
          & + ul {
            margin-top: 0.25rem;
          }
        }
        h4 {
          margin-top: 3.75rem;
        }
        p, ul, ul li {
          @include coTxtBody;
          color: $color-black-60;
        }
      }
      .terms-privacy-policy {
        margin-top: 2.5rem;
        ul {
          li {
            & ~ li {
              margin-top: 1.5rem;
            }
          }
          &.type1 {
            li {
              & ~ li {
                margin-top: 1.5rem;
              }
            }
          }
        }
        h2 {
          @include coTxtLabelLarge;
          color: $color-black-80;
          & + ul {
            margin-top: 0.25rem;
            li {
              &.table-list {
                & + li {
                  margin-top: 1.25rem;
                }
              }
            }
            & ~ h2 {
              margin-top: 3.75rem;
            }
          }
        }
        h3 {
          @include coTxtBody;
          color: $color-black-80;
          margin-top: 3.75rem;
        }
        p, ul, ul li {
          @include coTxtBody;
          color: $color-black-80;
        }
        
        p {
          margin-top: 0.25rem;
          & + ul {
            margin-top: 1.25rem;
          }
        }
        .table-wrap {
          margin-top: 1.25rem;
          table {
            width: max-content;
            &.terms-table-1 {
              tbody {
                tr {
                  td {
                    &:nth-child(1) {
                      width: 120px;
                    }
                    &:nth-child(2) {
                      width: 160px;
                    }
                    &:nth-child(3) {
                      width: 320px;
                    }
                    &:nth-child(4) {
                      width: 200px;
                    }
                    &:nth-child(5) {
                      width: 320px;
                    }
                    &:last-child {
                      text-align: center;
                    }
                  }
                }
              }
            }
            &.terms-table-2 {
              width: 51.25rem;
              thead tr th,
              tbody tr td {
                width: 33%;
              }
              thead tr th:nth-child(2),
              tbody tr td:nth-child(2) {
                width: 34%;
              }
            }
          }
          & + p {
            margin-top: 1.25rem;
          }
        }
      }
    }
  }
}

